@font-face {
  font-family: 'orbitron';  // orbitron-bold
  src: url('../fonts/orbitron-bold.otf') format('otf'),
  url('../fonts/orbitron-bold.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'Orbitron-Medium';
  src: url('../fonts/Orbitron-Medium.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: 'orbitron-regular';
  src: url('../fonts/orbitron-regular.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
